<script>
export default {
  name: "change-pswd-component",
  components: {},
  data() {
    // 較驗密碼
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("請輸入密碼"));
      } else {
        if (this.form.rePswd !== "") {
          this.$refs.form.validateField("rePswd");
        }
        callback();
      }
    };
    // 確認密碼
    var validateRePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("請再次輸入密碼"));
      } else if (value !== this.form.pswd) {
        callback(new Error("兩次密碼輸入不正確!"));
      } else {
        callback();
      }
    };
    return {
      formData: {},
      s_id: "",
      form: {
        pswd: "",
        rePswd: "",
      },
      rules: {
        pswd: [
          { required: true, message: "必填", trigger: "blur" },
          { validator: validatePass, trigger: "blur" },
        ],
        rePswd: [
          { required: true, message: "必填", trigger: "blur" },
          { validator: validateRePass, trigger: "blur" },
        ],
      },
    };
  },
  computed: {},
  methods: {
    submit(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.formData.PASSWORD = this.form.rePswd;
          this.updSupplierDetail(this.formData.PASSWORD);
        } else {
          this.$message({
            type: "error",
            showClose: true,
            message: "資料輸入錯誤",
          });
        }
      });
    },
    goBack() {
      this.$router.go(-1);
    },
    async updSupplierDetail(data) {
      try {
        await this.$store.dispatch("resetPD", data);
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message: "修改成功",
        });
      } catch (err) {
        console.log(err);
      }
    },
    async getSupplierDetail(id) {
      try {
        const res = await this.$store.dispatch("getSupplierDetail", id);
        this.formData = res.data.data;
      } catch (err) {
        console.log(err);
      }
    },
  },
  async mounted() {
    this.s_id = this.$route.params.s_id;
    this.getSupplierDetail(this.s_id);
    this.$root.$children[0].sidebarSwitch = true;
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
  },
};
</script>

<template src="./changePswd.html"></template>
